)\r\n        },\r\n\r\n        // 模糊搜索市场负责人\r\n        addMarketManager (items, targetVM) {\r\n            if ($.inArray(items, this.marketManagers) === -1 && items) {\r\n                this.marketManagers.push(items) \r\n            }\r\n            \r\n            targetVM.reset()\r\n        },\r\n\r\n        // 删除负责人\r\n        deleteUser (index, name) {\r\n            this[name].splice(index, 1)\r\n        },\r\n\r\n        // 新增产品\r\n        saveFn () {\r\n            if (this.businessType === '1') {\r\n\r\n                if (this.gameList && \r\n                    this.department && this.productLevel && this.gameType && this.platformType\r\n                    && this.developModel && this.phase && this.maintainManagers.length) {\r\n\r\n                    this.saveVaild()\r\n                    \r\n                } else {\r\n                    this.$dispatch('show-notify', '存在未填写的必填项，请检查')\r\n                }\r\n\r\n            } else if (this.businessType === '2') {\r\n\r\n                if (this.department && this.productLevel && this.maintainManagers.length) {\r\n\r\n                    this.saveVaild()\r\n                    \r\n                } else {\r\n                    this.$dispatch('show-notify', '存在未填写的必填项，请检查')\r\n                }\r\n            } else {\r\n                this.$dispatch('show-notify', '请选择业务类型')\r\n            }\r\n        },\r\n\r\n        // 执行保存\r\n        saveVaild () {\r\n            this.$http({\r\n                url: '/product/operate/',\r\n                method: 'POST',\r\n                data: this.$data\r\n            })\r\n            .then(response => {\r\n                if (response.data.code === 200) {\r\n                    this.creatProductModal = false\r\n                    this.$data = Object.assign({}, init)\r\n                    this.getBusinessSearch()\r\n\r\n                    this.$dispatch('refresh')\r\n                    this.$dispatch('show-success')\r\n                } else {\r\n      